/* Colors:
 *      Name                    Hex
 *      Primary (dark blue)     #23615f
 *      Secondary (orange)      #f7634f
 */

.App {
    background-image: url("./imgs/background_2.png");
    background-repeat: true;
}

.Header {
    position: fixed;
    width: 100%;
    height: 60px;
    z-index: 10;
}

.Spacer {
    height: 60px;
}

.Home {
    height: 700px;
    background-color: #23615f;
}

.Home img {
    animation: rotation 40s infinite linear; 
    position: absolute;
    width: 700px;
    height: 700px;
    overflow: hidden;
}

.About {
    height: 800px;
}

.About img {
    margin-top: 30px;
    margin-bottom: 30px;
    border: 5px solid #23615f;
    border-radius: 50%;
}

.Projects {
    height: 800px;
}

.Projects img {
    max-width: 350px;
    margin-top: 20px;
    border: 5px solid #23615f;
    border-radius: 50%;
}

.Contact {
    height: 800px;
}

h1 {
    color: white;
}

h2 {
    height: 80px;
    color: #f7634f;
}

h5 {
    margin-left: 10%;
    margin-right: 10%;
    color: #23615f;
}

p {
    color: white;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

@media only screen and (max-width: 700px) {
    .Home {
        height: 300px;
    }
    .Home img {
        display: none;
    }
}

